import { Report } from 'notiflix/build/notiflix-report-aio';
import { Notify } from 'notiflix/build/notiflix-notify-aio';

//            .

const reportOptions = {
  svgSize: '50px',
  svgColor: '#ff6b01',
  buttonBackground: '#ff6b01',
  titleMaxLength: 80,
  backOverlayClickToClose: true,
  cssAnimationDuration: 500,
};

const notifyOptions = {
  clickToClose: true,
};

//     ,       .   catch.
export function errorNotification() {
  Report.failure(
    'There are some problems! Try again later.',
    '',
    'Try again',
    reportOptions
  );
}

//   ,       .
export function noMatchesNotification() {
  Report.failure(
    'Sorry, there are no movies matching your search query! Please try again.',
    '',
    'Try again',
    reportOptions
  );
}

//       ,    submit   .
export function emptyQueryNotification() {
  Report.failure('Please enter your search query!', '', 'OK', reportOptions);
}

//          
export function successfulAddToWatchedNotification() {
  Notify.info('The movie was successfully added to Watched', notifyOptions);
}

//          
export function successfulAddToQueueNotification() {
  Notify.info('The movie was successfully added to Queue', notifyOptions);
}

//         
export function successfulDeleteFromWatchedNotification() {
  Notify.info('The movie was successfully removed from Watched', notifyOptions);
}

//         
export function successfulDeleteFromQueueNotification() {
  Notify.info('The movie was successfully removed from Queue', notifyOptions);
}

//      ,     
export function isNotInWatchedNotification() {
  Notify.warning('The movie is not found in Watched', notifyOptions);
}

//    ,  ,     
export function isNotInQueueNotification() {
  Notify.warning('The movie is not found in Queue', notifyOptions);
}

export function failRequest() {
  Notify.warning('Why did you deliberately break the code?', notifyOptions);
}

export function successfulRegistration() {
  Notify.info('Thank you for registration. Please sign in!', notifyOptions);
}

export function successfulSignIn() {
  Notify.info('Thank you for coming back!', notifyOptions);
}

export function successfulSignOut() {
  Notify.info('You have successfully signed out!', notifyOptions);
}

export function newPassword() {
  Notify.info(
    'Letter with new password was sent to your email.',
    notifyOptions
  );
}

export function userExistNotification() {
  Report.failure(
    'This user is already exist. Please sign in!',
    '',
    'OK',
    reportOptions
  );
}

export function incorrectPasswordNotification() {
  Report.failure(
    'Authorization error. Incorrect password',
    '',
    'OK',
    reportOptions
  );
}

export function invalidEmailNotification() {
  Report.failure('Please enter valid email!', '', 'OK', reportOptions);
}

export function missingEmailNotification() {
  Report.failure('Please enter email!', '', 'OK', reportOptions);
}

export function missingPasswordNotification() {
  Report.failure('Please enter password!', '', 'OK', reportOptions);
}

export function weekPasswordNotification() {
  Report.failure(
    'Password should be at least 6 characters.',
    '',
    'OK',
    reportOptions
  );
}

export function userNotFoundNotification() {
  Report.failure('User not found! Please register!', '', 'OK', reportOptions);
}
